let glob = {}

glob.CANUSEDOM = !!(
  typeof window !== 'undefined' &&
  window.document &&
  window.document.createElement
)

if(glob.CANUSEDOM){
  Object.assign(window, glob)
}
else {
  // Nook
  glob.console = {
    log: function(){},
    error: function(){},
    warn: function(){}
  }
  Object.assign(global, glob)
}

if(!glob.ga) glob.ga = false
if(!glob.DM_Context) glob.DM_Context = {}

String.prototype.trans = function(){return this}

export default glob



